@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon/icomoon.eot?r65i3b");
  src: url("../fonts/icomoon/icomoon.eot?r65i3b#iefix")
      format("embedded-opentype"),
    url("../fonts/icomoon/icomoon.ttf?r65i3b") format("truetype"),
    url("../fonts/icomoon/icomoon.woff?r65i3b") format("woff"),
    url("../fonts/icomoon/icomoon.svg?r65i3b#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook::after {
  content: "\e900";
}
